<template>
  <div ref="agent">
    <b-card class="bg-dailer mb-0 border-0" body-class="p-4 m-2">
      <Header from="agent" />
      <div v-if="status == 'connected'">
        <Timer :app_name="app_name" :connection="connection" :phone_number="number" v-if="is_start_timer" ref="timer" />
        <KeyPad :connection="connection" v-if="isKeypad == true" @digitPressed="digitPressed" @phoneNumber="changeNumber" ref="keypad" />
        <ConferenceCall
          :connection="connection"
          :twilioMute="twilioMute"
          @showkeypad="isKeypad = !isKeypad"
          @mutecall="muteCall()"
          @conferencecall="conferenceCall"
          v-if="connectedCall && !isKeypad"
        />
        <div class="align-items-center d-flex justify-content-between mb-3 pb-1 w-100" v-if="connectedCall && !isKeypad">
          <VoiceEmail ref="voiceemail" :connection="connection" />
          <PlayMessage :connection="connection" />
          <HoldCall />
        </div>
        <TransferCall :connection="connection" @transfercall="transferCall()" v-if="connectedCall && !isKeypad" />
        <div class="d-flex justify-content-center pt-2">
          <HangupCall v-if="is_start_timer" :connection="connection" @disconnectcall="disconnect_call()" />
          <b-button v-if="!is_start_timer" class="rounded-circle dailer-pad-number" type="button" variant="success" @click="initiateCall" title="Make call" v-b-tooltip:hover>
            <div class="align-items-center d-flex flex-column justify-content-center text-center">
              <div>
                <i class="fe fe-phone" />
              </div>
            </div>
          </b-button>
          <b-button type="button" variant="secondary" class="rounded-circle dailer-pad-number ml-3" @click="showkeypad()" title="Dial Pad" v-b-tooltip:hover v-if="isKeypad && connectedCall">
            <div class="align-items-center d-flex flex-column justify-content-center text-center">
              <div>
                <i class="fe fe-grid"></i>
              </div>
              <p class="mb-0 text">keypad</p>
            </div>
          </b-button>
        </div>
      </div>
    </b-card>
  </div>
</template>

<script>
  import { mapGetters } from "vuex";
  import Header from "./Header.vue";
  import Timer from "./Timer.vue";
  import KeyPad from "./KeyPad.vue";
  import ConferenceCall from "./agent/ConferenceCall.vue";
  import HangupCall from "./HangupCall.vue";
  import TransferCall from "./agent/TransferCall.vue";
  import HoldCall from "./agent/HoldCall.vue";
  import call from "../../js/dialer/call";
  import JWT from "../../js/dialer/jwt";
  import VoiceEmail from "./agent/VoiceEmail.vue";
  import PlayMessage from "./agent/PlayMessage.vue";
  import { bus } from "@/js/dialer/emit-bus";

  export default {
    components: { Header, Timer, KeyPad, ConferenceCall, HangupCall, TransferCall, HoldCall, VoiceEmail, PlayMessage },
    props: {
      number: String,
      app_name: {
        type: String,
      },
      is_make_call: {
        type: Boolean,
        default: true,
      },
      is_start_timer: {
        type: Boolean,
        default: true,
      },
    },
    data() {
      return {
        capability_url: "",
        connection: {},
        twilioConnection: {},
        status: "",
        twilioMute: false,
        isKeypad: false,
        connectedCall: false,
        numbers: [],
        conference_call: false,
      };
    },
    computed: {
      ...mapGetters(["isTwilioReady", "device", "checkDeviceStatus"]),
    },
    watch: {
      isTwilioReady(data) {
        if (data && this.is_make_call) {
          this.initiateCall();
        }
      },
      checkDeviceStatus(data) {
        if (data === "connect") {
          this.connectedCall = true;
          this.status = "connected";
        } else if (data == "disconnect") {
          this.connectedCall = false;
          this.is_start_timer = false;
          if (!this.is_make_call) {
            this.status = "connected";
            this.isKeypad = true;
          } else this.status = "";
        }
      },
    },
    mounted: async function() {
      bus.$on("incoming", (conn) => {
        // Incoming call check and post the messsage
        if (conn.type == "incoming" && !this.is_make_call) {
          this.twilioConnection = conn.conn;
          var incomming_data = {};
          incomming_data.direction = conn.conn.direction;
          incomming_data.from = conn.conn.parameters.From;
          incomming_data.callSid = conn.conn.CallSid;
          incomming_data.type = "IncomingCall";
          window.parent.postMessage(incomming_data, "*");
        }
        // Incoming call Accept
        if (conn.response == "Accept" && !this.is_make_call) {
          this.twilioConnection.accept();
          this.is_start_timer = true;
          this.status = "connected";
          this.connectedCall = true;
          this.number = conn.number;
          this.isKeypad = false;
          var show_dialer_obj = {};
          show_dialer_obj.type = "showDialer";
          window.parent.postMessage(show_dialer_obj, "*");
        }
        // Incoming call Reject
        if (conn.response == "Reject" && !this.is_make_call) {
          this.twilioConnection.reject();
        }
      });
      let token = JWT.jwt_token;
      let response = await call.getCapabilityAgent(this.app_name);
      this.$store.dispatch("getToken", `${response.pref_value}?To=PBX_${token.tenant_id}_${token.user_id}`);
      if (!this.is_make_call) {
        this.isKeypad = true;
        this.status = "connected";
      }
    },
    methods: {
      showkeypad() {
        this.isKeypad = !this.isKeypad;
      },
      initiateCall() {
        if (this.number.length > 5 && this.number != "number") {
          window.self = this;
          this.connectedCall = true;
          this.isKeypad = false;
          this.$nextTick(() => {
            this.is_start_timer = true;
          });
          this.connection = this.device.connect({ To: this.number });
          this.$store.dispatch("twilioDeviceListeners");
          this.twilioConnection = this.connection;
          if (this.app_name) this.$emit("callConnected", this.twilioConnection);
        }
      },
      disconnect_call() {
        this.number = "";
        this.connectedCall = false;
        this.is_start_timer = false;
        this.status = "connected";
        var disconnect_data = {};
        disconnect_data.direction = this.twilioConnection.direction;
        disconnect_data.callSid = this.twilioConnection.parameters.CallSid;
        disconnect_data.toNumber = this.twilioConnection.message.To;
        disconnect_data.type = "Disconnect Call";
        window.parent.postMessage(disconnect_data, "*");
        this.device.disconnectAll();
      },
      muteCall() {
        this.twilioMute = !this.twilioMute;
        this.device.activeConnection().mute(this.twilioMute);
      },
      digitPressed(number) {
        if (this.is_start_timer) this.twilioConnection.sendDigits(number.toString());
        if (this.number == "number") this.number = "";
        this.number += number.toString();
      },
      conferenceCall(number) {
        this.conference_call = true;
        this.numbers.push(number);
      },
      changeNumber(number) {
        return (this.number = number);
      },
    },
  };
</script>
